<template>
  <div>
    <div class="w-full mb-12 xl:mb-0 px-4 py-3">
      <h4 class="relative font-medium leading-tight text-4xl mt-0 mb-2 text-white">Json To Dart</h4>
    </div>
    <div class="w-full mb-12 xl:mb-0 px-4">
      <div class="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded bg-white">
        <div class="p-4 flex flex-col">
          <div class="mb-3">
            <input type="text" class="border-1 px-3 py-3 text-blueGray-600 relative bg-white rounded text-sm focus:outline-none focus:ring w-1/2" placeholder="First Class name" v-model="firstClassName">
          </div>
          <div>
            <textarea v-model="jsonInput" id="jsonText" rows="10" class="border px-3 py-3 text-blueGray-600 relative bg-white rounded text-sm  focus:outline-none focus:ring w-full overflow-y-scroll" placeholder="paste or type JSON here..."></textarea>
          </div>
          <div>
            <div class="text-red-500 my-2" v-if="jsonInput==null">Json input cannot empty</div>
            <div class="text-red-500 my-2" v-if="jsonError!=null">{{ jsonError }}</div>
            <div class="text-green-500 my-2" v-if="jsonError==null">Valid JSON âœ”</div>
          </div>
          <div>
            <button class="bg-emerald-600 hover:bg-emerald-600 text-white font-bold py-2 px-4 rounded-full" type="button" id="convertBtn" name="convertBtn" @click="executeConvertion">Convert</button>
          </div>
        </div>
      </div>
    </div>
    <div class="w-full mb-12 xl:mb-0 px-4" v-if="classes.length >0">
      <div class="relative flex flex-col w-full break-words w-full mb-6 shadow-lg rounded bg-white">
        <div class="p-4 flex flex-wrap">
          <div class="mb-3 w-full" v-for="(item, index) in classes" v-bind:key="index">
            <div class="flex flex-col border border-gray rounded-lg p-3 overflow-auto">
              <div class="flex flex-row content-center items-center">
                <!-- <input type="checkbox" value="false" class="mr-3"> -->
                <button class="bg-emerald-500 hover:bg-blue-700 text-white font-bold py-1 px-2 rounded-full text-xs mr-2" type="button" @click="copyClass(item)"><i class="feather icon-copy text-xs"></i> copy</button>
                <button class="bg-emerald-500 hover:bg-green-700 text-white font-bold py-1 px-2 rounded-full text-xs mr-2" type="button" @click="download(item)"><i class="feather icon-download text-xs"></i> Download</button>
                <p class="flex flex-grow">{{item.className}}.dart</p>
              </div>
              <div class="flex flex-grow">
                <hr class="my-2 w-full">
              </div>
              <pre>{{ showAsModel(item) }}</pre>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import JsonUtil from '@/utils/json/JsonUtil.js'
import ClassUtil from '@/utils/class/ClassUtil.js'
export default {
  name: "dashboard-page",
  components: {
  },
  data(){
    return {
      jsonInput:"",
      jsonError: "",
      firstClassName:null,
      classes:[],
    }
  },
  methods:{
    executeConvertion(){
      if(this.jsonInput!=null){
        this.classes = []
        this.jsonError = null
        let className = (this.firstClassName!==null && this.firstClassName !== "" && this.firstClassName !=="null" ) ? this.firstClassName : 'ExampleClass'
        JsonUtil.initClass(this.jsonInput, className)
        this.classes = JsonUtil.retrieveClass()
        let formatedJson = this.prettyFormat()
        if(formatedJson!=null)this.jsonInput =formatedJson
      }
    },
    showAsModel(item){
      return ClassUtil.showAsModel(item)
    },
    copyClass(item){
      let text = this.showAsModel(item)
      navigator.clipboard.writeText(text).then(function(){
        alert(`Class ${item.className} copied`);
      })
    },
    download(item) {
        let text = this.showAsModel(item)
        const file = new Blob([text], {type: 'text/plain'})
        let a = document.createElement('a'),url = URL.createObjectURL(file);
        a.href = url;
        a.download = `${item.className}.dart`;
        document.body.appendChild(a);
        a.click();
        setTimeout(function() {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);  
        }, 0); 
    },
    prettyFormat() {
        this.jsonError = null;
        let jsonValue = "";
        try {
            jsonValue = JSON.parse(this.jsonInput);
        }
        catch(e) {
            this.jsonError = JSON.stringify(e.message)
            var textarea = document.getElementById("jsonText");
            if (this.jsonError.indexOf('position')>-1) {
                var positionStr = this.jsonError.lastIndexOf('position') + 8;
                var posi = parseInt(this.jsonError.substr(positionStr,this.jsonError.lastIndexOf('"')))
                if (posi >= 0) {
                    textarea.setSelectionRange(posi,posi+1);
                }
            }
            return null;
        }
        return JSON.stringify(jsonValue, null, "\t");
    },
  },
};
</script>
